import React from 'react';
import './App.css';

import Items, { getItemById } from './data/item';
import Recipes, { Recipe } from './data/recipe';

import { Input } from 'reactstrap';

const App = () =>
{
	const [fabricatorList, setFabricatorList] = React.useState<Recipe[]>([]);

	React.useEffect(() =>
	{
		setFabricatorList(Recipes.fabricatorRecipe);
	}, []);

	const fabFilter = (e: React.ChangeEvent<HTMLInputElement>) =>
	{
		const keyword = e.currentTarget.value.toLowerCase();
		const filteredList = Recipes.fabricatorRecipe.filter(item =>
		{
			const match1 = item.product.some(item =>
			{
				const _item = getItemById(item.id);
				return _item.name.toLowerCase().indexOf(keyword) > - 1 || _item.name_locale.toLowerCase().indexOf(keyword) > -1;
			})

			if (match1) return true;

			const match2 = item.material.some(item =>
			{
				const _item = getItemById(item.id);
				return _item.name.toLowerCase().indexOf(keyword) > - 1 || _item.name_locale.toLowerCase().indexOf(keyword) > -1;
			})

			return match2;
		});
		setFabricatorList(filteredList);
	}

	return <div className="base">
		<header className="header unselectable" style={{ position: "relative", overflow: "hidden" }}>
			<div className="px-2" style={{ position: "absolute", zIndex: 1, fontSize: "24px" }}>Barotrauma Tool</div>
			<div style={{ position: "absolute", right: "0px", top: "0px" }}>
				<img src="img/barotrauma-2.png" style={{ height: "240px", transform: "translate(0px, -50%)" }} />
			</div>
		</header>
		<div className="main d-flex">
			<div className="p-2" style={{ flex: "1 0 0", background: "#dcdddf" }}>
				<div className="py-2">
					<div className="border-bottom border-dark mb-2 w3-large">製造台配方</div>
					<Input onChange={fabFilter} placeholder="搜尋" />
				</div>
				<table className="w3-table w3-border w3-bordered w3-striped" >
					<thead>
						<tr className="w3-indigo">
							<th>成品</th>
							<th>材料</th>
						</tr>
					</thead>
					<tbody>
						{
							fabricatorList.map((item, index) =>
							{
								const product = item.product[0];
								const productItem = Items.find(item => item.id === product.id)!;
								const materials = item.material;

								return <tr key={index}>
									<td style={{ width: "120px" }}>
										<img className="item" src={productItem.img_path} alt="" />
										<div>{productItem.name_locale}</div>
									</td>
									<td >
										{
											materials.map((item, index) =>
											{
												const materialItem = Items.find(_item => _item.id === item.id)!;
												return <div key={index} className="d-inline-block px-1">
													<img className="item" src={materialItem.img_path} />
													<div>{item.amount > 1 ? `${item.amount}x ` : null}
														{materialItem.name_locale}</div>
												</div>
											})
										}
									</td>
								</tr>;
							})
						}
					</tbody>
				</table>
			</div>
			<div style={{ flex: "1 0 0" }}>

			</div>

		</div>
	</div>;
}

export default App;
